.navBar {
  height: 60px;
  display: flex;
  align-items: center;
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.1);
}

.navComponent {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 2rem;
  cursor: pointer;
}

.selectedNavComponent {
  color: #1899bb;
  text-decoration: none;
  font-weight: bold;
  padding: 2rem;
  cursor: pointer;
}
